import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import Navbar from './components/Navbar';
import AdminDashboard from './pages/AdminDashboard';
import Notifications from './components/Notifications';
import CalendarView from './components/CalendarView';
import CompanyForm from './components/CompanyForm';
import CommunicationForm from './components/CommunicationForm';
import CompanyList from './components/CompanyList';
import Login from './pages/Login'; 

const App = () => {
  const userRole = localStorage.getItem('role'); // Fetch the role from localStorage

  //Function to handle form submission for CommunicationForm
  const handleCommunicationSubmit = (data) => {
    console.log('Communication Data:', data);
  };

  //Function to handle form submission for CompanyForm
  const handleCompanySubmit = (data) => {
    console.log('Company Data:', data);
  };

  // ProtectedRoute component for role-based routing
  const ProtectedRoute = ({ children, role }) => {
    if (!role || role !== userRole) {
      return <Navigate to="/login" replace />;
    }
    return children;
  };


  return (
    <Router>
      <div className="App">
        {/* Navbar visible on all routes */}
        <Navbar />
        <Routes>
          {/* Public Routes */}
          <Route path="/login" element={<Login />} />

          {/* Protected Routes */}
          <Route
            path="/"
            element={
              <ProtectedRoute role="admin">
                <AdminDashboard />
              </ProtectedRoute>
            }
          />
          <Route
            path="/notifications"
            element={
              <ProtectedRoute role="admin">
                <Notifications />
              </ProtectedRoute>
            }
          />
          <Route
            path="/calendar"
            element={
              <ProtectedRoute role="admin">
                <CalendarView />
              </ProtectedRoute>
            }
          />
          <Route
            path="/company-form"
            element={
              <ProtectedRoute role="admin">
                <CompanyForm onSubmit={handleCompanySubmit} />
              </ProtectedRoute>
            }
          />
          <Route
            path="/communication-form"
            element={
              <ProtectedRoute role="admin">
                <CommunicationForm onSubmit={handleCommunicationSubmit} />
              </ProtectedRoute>
            }
          />
          <Route
            path="/companies"
            element={
              <ProtectedRoute role="admin">
                <CompanyList />
              </ProtectedRoute>
            }
          />
          {/* Fallback Route */}
          <Route path="*" element={<h1>Page Not Found</h1>} />
        </Routes>
      </div>
    </Router>
  );
};

export default App;
