import React from 'react';
import { Box, Grid, Card, CardContent, Typography, Divider } from '@mui/material';

function AdminDashboard() {
  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3, p: 3, backgroundColor: '#f5f5f5', minHeight: '100vh' }}>
      <Typography variant="h4" sx={{ fontWeight: 'bold' }}>Admin Dashboard</Typography>

      <Grid container spacing={3}>
        {/* Companies Section */}
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6">Companies</Typography>
              <Divider sx={{ my: 2 }} />
              <Typography variant="body1" sx={{ fontWeight: 'bold' }}>Name:</Typography>
              <Typography variant="body2">Tech Innovators</Typography>
              <Typography variant="body1" sx={{ fontWeight: 'bold', mt: 2 }}>Location:</Typography>
              <Typography variant="body2">San Francisco, CA</Typography>
              <Typography variant="body1" sx={{ fontWeight: 'bold', mt: 2 }}>LinkedIn Profile:</Typography>
              <Typography variant="body2">
                <a href="https://www.linkedin.com/company/tech-innovators" target="_blank" rel="noopener noreferrer">
                  Tech Innovators LinkedIn
                </a>
              </Typography>
              <Typography variant="body1" sx={{ fontWeight: 'bold', mt: 2 }}>Email:</Typography>
              <Typography variant="body2">contact@techinnovators.com</Typography>
              <Typography variant="body1" sx={{ fontWeight: 'bold', mt: 2 }}>Phone:</Typography>
              <Typography variant="body2">+1-234-567-8901</Typography>
              <Typography variant="body1" sx={{ fontWeight: 'bold', mt: 2 }}>Periodicity:</Typography>
              <Typography variant="body2">Every 2 weeks</Typography>
            </CardContent>
          </Card>
        </Grid>

        {/* Communications Section */}
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6">Communications</Typography>
              <Divider sx={{ my: 2 }} />
              <Typography variant="body1" sx={{ fontWeight: 'bold' }}>LinkedIn Post:</Typography>
              <Typography variant="body2">"Exciting new features coming soon!"</Typography>
              <Typography variant="body1" sx={{ fontWeight: 'bold', mt: 2 }}>Date:</Typography>
              <Typography variant="body2">January 5, 2025</Typography>
              <Typography variant="body1" sx={{ fontWeight: 'bold', mt: 2 }}>Notes:</Typography>
              <Typography variant="body2">The post will highlight our upcoming product updates.</Typography>
            </CardContent>
          </Card>
        </Grid>

        {/* Tasks Section */}
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6">Tasks</Typography>
              <Divider sx={{ my: 2 }} />
              
              {/* Overdue Tasks */}
              <Typography variant="subtitle2" sx={{ fontWeight: 'bold' }}>Overdue Tasks:</Typography>
              <Typography variant="body2">- Follow up with Client A (Deadline: Dec 28, 2024)</Typography>
              <Typography variant="body2">- Submit report to management (Deadline: Dec 30, 2024)</Typography>

              <Divider sx={{ my: 2 }} />

              {/* Due Today */}
              <Typography variant="subtitle2" sx={{ fontWeight: 'bold' }}>Due Today:</Typography>
              <Typography variant="body2">- Prepare LinkedIn post draft</Typography>
              <Typography variant="body2">- Schedule team meeting at 3:00 PM</Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Box>
  );
}

export default AdminDashboard;
