import React, { useState } from 'react';
import {
  TextField,
  MenuItem,
  Button,
  Typography,
  Card,
  CardContent,
  Divider,
} from '@mui/material';

function CompanyForm({ onSubmit }) {
  const [formData, setFormData] = useState({
    company: '',
    location: '',
    linkedIn: '',
    email: '',
    phone: '',
    periodicity: '1 Week',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit(formData);
    setFormData({
      company: '',
      location: '',
      linkedIn: '',
      email: '',
      phone: '',
      periodicity: '1 Week',
    });
  };

  return (
    <Card sx={{ maxWidth: 600, margin: 'auto', mt: 5, boxShadow: 3 }}>
      <CardContent>
        <Typography variant="h5" sx={{ fontWeight: 'bold', mb: 2, textAlign: 'center' }}>
          Add Company
        </Typography>
        <Divider sx={{ mb: 3 }} />
        <form onSubmit={handleSubmit}>
          <TextField
            fullWidth
            label="Company"
            name="company"
            value={formData.company}
            onChange={handleChange}
            sx={{ mb: 3 }}
            required
          />
          <TextField
            fullWidth
            label="Location"
            name="location"
            value={formData.location}
            onChange={handleChange}
            sx={{ mb: 3 }}
            required
          />
          <TextField
            fullWidth
            label="LinkedIn Profile"
            name="linkedIn"
            value={formData.linkedIn}
            onChange={handleChange}
            sx={{ mb: 3 }}
            required
          />
          <TextField
            fullWidth
            label="Email"
            name="email"
            type="email"
            value={formData.email}
            onChange={handleChange}
            sx={{ mb: 3 }}
            required
          />
          <TextField
            fullWidth
            label="Phone"
            name="phone"
            type="tel"
            value={formData.phone}
            onChange={handleChange}
            sx={{ mb: 3 }}
            required
          />
          <TextField
            select
            fullWidth
            label="Periodicity"
            name="periodicity"
            value={formData.periodicity}
            onChange={handleChange}
            sx={{ mb: 3 }}
          >
            <MenuItem value="1 Week">1 Week</MenuItem>
            <MenuItem value="2 Weeks">2 Weeks</MenuItem>
            <MenuItem value="1 Month">1 Month</MenuItem>
          </TextField>
          <Button type="submit" variant="contained" color="primary" fullWidth>
            Submit
          </Button>
        </form>
      </CardContent>
    </Card>
  );
}

export default CompanyForm;
