import React, { useState } from 'react';
import {
  TextField,
  MenuItem,
  Button,
  Typography,
  Card,
  CardContent,
  Divider,
} from '@mui/material';

function CommunicationForm({ onSubmit }) {
  const [formData, setFormData] = useState({
    communicationType: '',
    date: '',
    notes: '',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit(formData);
    setFormData({
      communicationType: '',
      date: '',
      notes: '',
    });
  };

  return (
    <Card sx={{ maxWidth: 600, margin: 'auto', mt: 5, boxShadow: 3 }}>
      <CardContent>
        <Typography variant="h5" sx={{ fontWeight: 'bold', mb: 2, textAlign: 'center' }}>
          Add Communication
        </Typography>
        <Divider sx={{ mb: 3 }} />
        <form onSubmit={handleSubmit}>
          <TextField
            select
            fullWidth
            label="Select Communication Type"
            name="communicationType"
            value={formData.communicationType}
            onChange={handleChange}
            sx={{ mb: 3 }}
            required
          >
            <MenuItem value="LinkedIn Post">LinkedIn Post</MenuItem>
            <MenuItem value="LinkedIn Message">LinkedIn Message</MenuItem>
            <MenuItem value="Email">Email</MenuItem>
            <MenuItem value="Phone Call">Phone Call</MenuItem>
            <MenuItem value="Other">Other</MenuItem>
          </TextField>
          <TextField
            fullWidth
            label="Date"
            name="date"
            type="date"
            value={formData.date}
            onChange={handleChange}
            sx={{ mb: 3 }}
            InputLabelProps={{
              shrink: true,
            }}
            required
          />
          <TextField
            fullWidth
            label="Notes"
            name="notes"
            multiline
            rows={4}
            value={formData.notes}
            onChange={handleChange}
            sx={{ mb: 3 }}
          />
          <Button type="submit" variant="contained" color="primary" fullWidth>
            Submit
          </Button>
        </form>
      </CardContent>
    </Card>
  );
}

export default CommunicationForm;
